/**
 * AI
 */
export interface AIMessage {
    role: 'system' | 'user' | 'assistant';
    content: string;
}

/**
 * OpenAIAPI
 */
export interface OpenAICompatibleOptions {
    apiUrl: string; // OpenAIAPI
    apiKey: string; // API
    model: string; // 
    maxTokens?: number; // token
    temperature?: number; // 
    abortSignal?: AbortSignal; // 
}

/**
 * OpenAIAPI
 * @param messages 
 * @param options  OpenAIAPI
 * @returns        AI
 * @throws         
 */
export async function callOpenAICompatible(
    messages: AIMessage[],
    options: OpenAICompatibleOptions
): Promise<string> {
    // API/v1
    const baseUrl = options.apiUrl.replace(/\/$/, '').replace(/\/v1$/, '');
    const apiUrl = `${baseUrl}/v1/chat/completions`;
    const fetchOptions: RequestInit = {
        method: 'POST',
        headers: {
            Authorization: `Bearer ${options.apiKey}`,
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            model: options.model,
            messages: messages,
            max_tokens: options.maxTokens,
            temperature: options.temperature,
            stream: false,
        }),
    };

    if (options.abortSignal) {
        fetchOptions.signal = options.abortSignal;
    }

    const response = await fetch(apiUrl, fetchOptions);

    if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`OpenAIAPI: ${response.status} - ${errorText}`);
    }

    const responseData = await response.json();
    return responseData?.choices?.[0]?.message?.content ?? '';
}

/**
 * SillyTavern APIOpenAIAPI
 * @param messages 
 * @param settings 
 * @param abortSignal 
 * @returns AI
 */
export async function callSillyTavernOpenAI(
    messages: AIMessage[],
    settings: any,
    abortSignal?: AbortSignal
): Promise<string> {
    // getRequestHeaders
    const { getRequestHeaders } = await import('@sillytavern/script');

    // SillyTavernCSRF token
    const requestHeaders = getRequestHeaders();

    // SillyTavernAPI
    const requestBody = {
        reverse_proxy: settings.openaiApiUrl,
        proxy_password: settings.openaiApiKey || '', // SillyTavern
        chat_completion_source: settings.chat_completion_source || 'openai',
        model: settings.openaiModel,
        messages: messages,
        max_tokens: settings.openaiMaxTokens || 1000,
        temperature: settings.openaiTemperature || 0.7,
        stream: false,
        // SillyTavern
        custom_prompt_post_processing: false,
    };

    const fetchOptions: RequestInit = {
        method: 'POST',
        headers: requestHeaders,
        body: JSON.stringify(requestBody),
    };

    if (abortSignal) {
        fetchOptions.signal = abortSignal;
    }

    // SillyTavern
    const generate_url = '/api/backends/chat-completions/generate';

    try {
        const response = await fetch(generate_url, fetchOptions);

        if (!response.ok) {
            const errorText = await response.text();
            throw new Error(`SillyTavern OpenAI API: ${response.status} - ${errorText}`);
        }

        const responseData = await response.json();
        return responseData?.choices?.[0]?.message?.content ?? '';
    } catch (error) {
        console.error('API:', error);
        throw error;
    }
}
